<template>
  <div id="order" v-if="ord">
    <div class="order_top">
      <div class="order_t">
        <div class="order_t_l">
          <h2>订单信息</h2>
          <p>
            累计订单：<span>{{ ord.order_count }}</span
            >总消费：￥<span>{{ ord.sum_price }}</span>
          </p>
          <!-- 
                已退款 ord.refund_count -->
        </div>
        <div class="order_t_r">
          <van-image
            src="http://47.115.51.185/h5/img/orderTime.e95f9090.png"
            fit="cover"
          />
        </div>
      </div>
    </div>
    <div>
      <!-- 订单内容切换 -->
      <div class="order_icr">
        <div
          class="order_item"
          v-for="(i, index) in title"
          :key="index"
          :class="active == index ? 'on' : ''"
          @click="active = index"
        >
          <p>{{ i }}</p>
          <p>{{ ord[titles[index]] }}</p>
        </div>
      </div>
      <!-- 组件内容 -->
      <div class="order_content" v-if="orderList.length">
        <component :is="type" :orderList="orderList" @update="update"></component>
      </div>
      <!-- 不存在商品 -->
      <OrderNone v-else></OrderNone>
      <!-- 加载中提示 -->
      <van-loading color="#7F7F7F" type="spinner" vertical v-if="flag"
        >正在加载</van-loading
      >
    </div>
  </div>
</template>
<script>
// 代付款
import Obligations from "../components/order/Obligation.vue";
// 不存在商品
import OrderNone from "../components/order/OrderNone.vue";
// 待发货
import overhang from "../components/order/obligationPsd/Overhang.vue";
export default {
  components: {
    Obligations,
    OrderNone,
    overhang,
  },
  data() {
    return {
      ord: null,
      //订单切换
      active: 0,
      title: ["代付款", "待发货", "待收货", "待评价", "已完成"],
      titles: [
        "unpaid_count",
        "unshipped_count",
        "received_count",
        "evaluated_count",
        "complete_count",
      ],
      //   组件类型
      type: "Obligations",
      // 订单各类具体信息
      orderList: [],
      // 加载提示是否出现
      flag: true,
      ids: 0,
    };
  },
  created() {
    this.active = this.$route.params.id;
    this.orderNum();
    this.getOrderList(this.active);
  },
  //   监听activt
  watch: {
    active(news) {
      this.flag = true;
      this.$router.push("/order/list/" + news);
      this.getOrderList(this.active);
      switch (news) {
        case 0:
          this.type = "Obligations";
          break;
        case 1:
          this.type = "overhang";
          break;
        case 2:
          break;
        case 3:
          break;
        case 4:
          break;
      }
    },
    // 监控路由变化
    $route(to) {
      // console.log(to);
      this.active = to.params.id;
    },
  },
  //
  methods: {
    //路由参数改变，重新获取数据
    getOrderList(val) {
      // 代收款等的具体数据
      this.axios
        .get("/api/order/list?page=1&limit=20&type=" + val)
        .then((res) => {
          //  console.log(res);
          this.orderList = res.data.data;
          this.flag = false;
        });
    },
    // 各类订单数量
    orderNum() {
      // 订单各类数量
      this.axios.get("/api/order/data").then((res) => {
        this.ord = res.data.data;
        //   console.log(this.ord);
      });
    },
    // 当取消订单之后的更新
    update(){
      this.orderNum();
      this.getOrderList(this.active);
    }
  },
};
</script>
<style lang="less" scoped>
#order {
  background: #f5f5f5;
  width: 100vw;
  // height: 100vh;
  box-sizing: border-box;
  .order_top {
    background: #e93323 !important;
    box-sizing: border-box;
    padding: 0 0.4rem;
    height: 3.4rem;
    &.order_top + div {
      box-sizing: border-box;
      padding: 0 0.3rem;
    }
    .order_t {
      display: flex;
      align-items: center;
      height: 2.9rem;
      // 左
      .order_t_l {
        color: hsla(0, 0%, 100%, 0.8);
        font-size: 0.26rem;
        text-align: left;
        flex: 1;
        h2 {
          font-size: 0.56rem;
          font-weight: 700;
          color: #fff;
          margin-bottom: 0.2rem;
        }
        p {
          span:nth-child(1) {
            margin-right: 0.25rem;
          }
        }
      }
      // 右
      .order_t_r {
        width: 1.62rem;
        height: 1.49rem;
      }
    }
  }

  //内容切换
  .order_icr {
    background-color: #fff;
    // width: 8rem;
    height: 1.88rem;
    border-radius: 0.08rem;
    margin: -0.94rem auto 0.38rem auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    .order_item {
      text-align: center;
      font-size: 0.26rem;
      color: #282828;
      padding: 0.32rem 0 0.5rem 0;
      p:nth-child(2) {
        margin-top: 0.24rem;
      }
      &.on {
        font-weight: 700;
        border-bottom: 0.05rem solid #e93323;
      }
    }
  }
}
</style>