<template>
  <div class="obligationPsd" v-if="item">
    <!-- 代付款模板 -->
    <!-- 订单创建时间 -->
    <div class="add_time">
      <span class="time">{{ item._add_time }}</span>
      <span class="play_state">{{ item._status._title }}</span>
    </div>
    <!-- 商品信息 -->
    <div class="goodsInfo">
      <div class="goodsInfo_t">
        <div class="goodsInfo_t_l" @click="goods">
          <img :src="item.cartInfo[0].productInfo.image" />
        </div>
        <div class="goodsInfo_t_r" @click="order">
          <div class="goodsInfo_text van-multi-ellipsis--l2">
            {{ item.cartInfo[0].productInfo.store_name }}
          </div>
          <div class="goodsInfo_price">
            <p>￥{{ item.cartInfo[0].truePrice }}</p>
            <p>x{{ item.total_num }}</p>
          </div>
        </div>
      </div>
      <div class="goodsInfo_b">
        <p>
          共{{ item.total_num
          }}{{ item.cartInfo[0].productInfo.unit_name }}商品，总金额<span
            >￥{{ item.pay_price }}</span
          >
        </p>
      </div>
    </div>
    <!-- 订单操作 -->
    <div class="os">
      <!-- 待付款 -->
      <div class="payment" v-if="state==0">
        <button @click="cancelOrder">取消订单</button>
        <button @click="payFor">立即付款</button>
      </div>
      <!-- 待发货 -->
      <div class="overhang" v-if="state==1">
        <button class="btn_red" @click="seeDetails">查看详情</button>
      </div>
    </div>
    <!-- 点击立即付款的面板 -->
    <van-action-sheet v-model="show" title="选择付款方式">
      <Pay :keys="item.order_id"></Pay>
    </van-action-sheet>
  </div>
</template>
<script>
// 付款
import Pay from '../../OrderStatus/Pay'
export default {
  components:{
    Pay,
  },
  props: ["item","ids"],
  data(){
    return{
      order_key:null,
      // keys:null,
      show:false,
      // 商品id
      goods_id:null,
      // 判断现在是什么状态，0为待付款，1为待发货
      state:0,
    }
  },
  // this.item.order_id
  created() {
    this.state=this.$route.params.id;
    // this.keys=this.item.order_id;
    this.order_key=this.item.order_id;
    // console.log(this.item);
    this.goods_id=this.item.cartInfo[0].productInfo.id;
  },
  methods:{
    // 取消订单
    cancelOrder(){
      // /api/order/cancel
      this.axios.post("/api/order/cancel",{
        id:this.order_key
      }).then(res=>{
        console.log(res);
        this.$emit("cancel");
      })
    },
    // 立即付款
    payFor(){
      this.show=true;
    },
    // 点击商品图片，进入商品信息页面
    goods(){
      this.$router.push(`/detail/${this.goods_id}`);
    },
    // 点击商品的左侧信息，进入订单页面
    order(){
      this.$router.push(`/order/detail/${this.order_key}`);
    },
    // 待发货查看详情
    seeDetails(){
      this.$router.push(`/order/detail/${this.item.order_id}`)
    }
  }
};
</script>
<style lang="less" scoped>
.obligationPsd {
  background: #fff;
  border-radius: 0.06rem;
  margin-bottom: 0.24rem;
  //   width: 9rem;
  // 创建时间
  .add_time {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    height: 0.84rem;
    padding: 0 0.3rem;
    border-bottom: 0.01rem solid #eee;
    font-size: 0.28rem;
    color: #282828;
    .play_state {
      color: #fc4141;
      flex: 1;
      text-align: right;
    }
  }
  // 商品信息
  .goodsInfo {
    margin-top: 0.22rem;
    box-sizing: border-box;
    padding: 0 0.3rem;
    //   上
    .goodsInfo_t {
      display: flex;
      // 左
      .goodsInfo_t_l {
        width: 1.6rem;
        height: 1.6rem;
        overflow: hidden;
        img {
          width: 100%;
          height: auto;
          border-radius: 0.06rem;
        }
      }
      // 右
      .goodsInfo_t_r {
        flex: 1;
        box-sizing: border-box;
        font-size: 0.26rem;
        color: #282828;
        text-align: left;
        margin: 0.27rem 0 0 0.3rem;
        padding: 0 0.3rem 0.3rem 0;
        // border-bottom: 0.01rem solid #eee;
        display: flex;
        align-items: center;
        .goodsInfo_text {
          width: 4.86rem;
        }
        .goodsInfo_price {
          width: 1.2rem;
          flex: 1;
          text-align: right;
        }
      }
    }
    // 下
    .goodsInfo_b {
      p {
        box-sizing: border-box;
        font-size: 0.26rem;
        color: #282828;
        text-align: right;
        margin: 0.27rem 0 0 0.3rem;
        padding: 0 0.3rem 0.3rem 0;
        border-bottom: 0.01rem solid #eee;
        span {
          color: #fc4141 !important;
          font-weight: 700;
          font-size: 0.28rem;
        }
      }
    }
  }
  // 订单操作
  .os {
    box-sizing: border-box;
    padding: 0 0.3rem;
    height: 1.42rem;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    button {
      text-align: center;
      color: #fff;
      border-radius: 0.5rem;
      font-size: 0.27rem;
      border: none;
      padding: 0.15rem 0.3rem;
    }
    button:nth-child(1) {
      border: 1px solid #ddd;
      color: #aaa;
      margin-right: 0.5rem;
    }
    button:nth-child(2),.btn_red {
      background-color: #e93323 !important;
      color: #fff !important;
    }
    // 代付款
    .payment {
      display: flex;
      justify-content: flex-end;
      align-items: center;
    }
  }
}
</style>