<template>
  <div id="pay">
    <!-- 点击支付时跳出的微信支付和余额支付 -->
    <div class="weixin item" @click="pay(1)">
      <div>
        <i class="iconfont icon-weixinzhifu1"></i>
      </div>
      <div>
        <p>微信支付</p>
        <p>使用微信快捷支付</p>
      </div>
      <div><van-icon name="arrow" /></div>
    </div>
    <div class="yue item" @click="pay(2)">
      <div>
        <i class="iconfont icon-chongzhijilu1"></i>
      </div>
      <div>
        <p>余额支付</p>
        <p>
          当前可用余额：<span>{{ now_price }}</span>
        </p>
      </div>
      <div><van-icon name="arrow" /></div>
    </div>
    <!-- 支付加载 -->
    <van-loading size="54px" vertical text-color="#fff" v-if="flag"
      >加载中...</van-loading
    >
  </div>
</template>
<script>
export default {
  data() {
    return {
      now_price: null,
      flag: false,
    };
  },
  props: ["keys"],
  created() {
    // console.log(this.keys);
    // 余额查询
    this.axios.get("/api/userinfo").then((res) => {
      this.now_price = res.data.data.now_money;
    });
  },
  methods: {
    pay(index) {
      this.flag = true;
      let type = index == 1 ? "weixin" : "yue";
      this.axios
        .post("/api/order/pay", {
          paytype: type,
          uni: this.keys,
        })
        .then((res) => {
          this.flag = false;
          this.$toast({
              duration:500,
              message:res.data.msg
          });
        });
    },
  },
};
</script>
<style lang="less" scoped>
#pay {
  box-sizing: border-box;
  padding-bottom: 0.6rem;
  .weixin {
  }
  .yue {
    border-top: none !important;
    div:nth-child(1) {
      i {
        color: #f90 !important;
      }
    }
  }
  .item {
    box-sizing: border-box;
    border-bottom: 0.01rem solid #eee;
    border-top: 0.01rem solid #eee;
    margin-left: 0.3rem;
    padding-right: 0.3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 1.6rem;
    div:nth-child(1) {
      i {
        font-size: 0.6rem;
        color: #09bb07;
      }
    }
    div:nth-child(2) {
      flex: 1;
      text-align: left;
      margin-left: 0.42rem;
      p:nth-child(1) {
        font-size: 0.32rem;
        color: #282828;
      }
      p:nth-child(2) {
        font-size: 0.34rem;
        color: #999;
        margin-top: 0.1rem;
      }
    }
    div:nth-child(3) {
      i {
        font-size: 0.44rem;
        color: #999;
      }
    }
  }
}
</style>
<style lang="less">
#pay {
  //   加载
  .van-loading {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
</style>